body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-image: url("/asset/images/bg.png"); }

.text-left {
  text-align: left; }

.sidebar-closebtn {
  margin: 0px;
  margin-left: calc(100% - 49px); }

.round-shape {
  border-radius: 50%; }

.padding0 {
  padding: 0px; }

.paddingLR0 {
  padding-left: 0px;
  padding-right: 0px; }

.paddingLR0 {
  padding-left: 0px;
  padding-right: 0px; }

.paddingLR16 {
  padding-left: 0px;
  padding-right: 0px; }

.margin0 {
  margin: 0px; }

.marginLR16 {
  margin-left: 16px;
  margin-right: 16px; }

.fullWidth {
  width: 100%; }

.menu-icon {
  font-style: normal;
  font-size: 20px;
  padding-right: 8px; }

.footer-icon {
  height: 36px;
  width: 36px;
  display: block;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
  border-radius: 3px;
  transition: 0.3s all; }

