.App {
  overflow: hidden;
  position: relative; }

.menuList > div {
  transition: 0.5s all;
  border: 0px solid transparent; }
  .menuList > div:hover, .menuList > div .active {
    border-left: 3px solid gray; }

@media (min-width: 960px) {
  .left-menu-opened + main {
    padding-left: 15vw; }
  .left-menu-closed + main {
    padding-left: 0; }
  .left-menu-opened + main + footer {
    padding-left: 15vw; }
  .left-menu-closed + main + footer {
    padding-left: 0; } }
